@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *::before, *::after {
  box-sizing: border-box; }

body {
  text-align: center;
  font-family: 'Lato',serif;
  line-height: 1.5;
  font-weight: bold;
  /*overflow-y: scroll;
    overflow-x: hidden;*/ }

a {
  text-decoration: none; }

img {
  vertical-align: middle;
  max-width: 100%;
  height: auto; }

.container {
  margin: 0 auto;
  height: 100vh; }

.roboto {
  font-family: 'roboto',serif; }

.d-flex {
  display: flex; }

.justify-content-between {
  justify-content: space-between; }

.align-items-center {
  align-items: center; }

.flex-row {
  flex-direction: row; }

.flex-column {
  flex-direction: column; }

.icon {
  font-size: 24px !important;
  color: black !important; }
  .icon:hover {
    color: #198754 !important;
    position: relative; }

.vertical-lr {
  writing-mode: vertical-lr; }

.text-indent-28 {
  text-indent: 28px; }

.header-footer {
  width: 23.15%;
  padding: 32px;
  background-color: #F8F9FA; }
  .header-footer h1 {
    font-size: 40px;
    line-height: 0;
    margin-bottom: 32px; }
  .header-footer span {
    margin-right: 16px; }
  .header-footer p {
    font-size: 16px;
    margin-bottom: 29px; }
  .header-footer a {
    color: #212529; }
    .header-footer a:hover ::before {
      content: "";
      position: absolute;
      width: 4px;
      height: 32px;
      background: #198754;
      left: -32px;
      top: -4px; }

.footer {
  text-align: left; }
  .footer img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 16px; }
  .footer p {
    font-size: 16px;
    margin-bottom: 0;
    line-height: 1.2; }
  .footer span {
    font-size: 14px;
    color: #6C757D; }

.assignment {
  padding: 32px;
  width: 76.85%;
  height: 100vh; }

.tilte {
  color: #212529; }
  .tilte h2 {
    font-size: 24px;
    line-height: 33px;
    margin-right: 50px; }
  .tilte select {
    border: none;
    font-size: 16px;
    line-height: 1.5;
    font-weight: bold;
    outline: 0; }
    .tilte select:hover {
      cursor: pointer;
      color: #198754; }
  .tilte .search {
    margin-right: 26px; }
    .tilte .search:first-child {
      margin-right: 0; }

.assignment-title {
  margin-top: 48px;
  margin-bottom: 8px; }
  .assignment-title select {
    border: none;
    font-size: 16px;
    font-weight: bold;
    outline: 0; }
    .assignment-title select:hover {
      cursor: pointer;
      color: #198754; }
  .assignment-title a {
    color: #6C757D; }
    .assignment-title a:hover {
      opacity: 0.5; }
  .assignment-title li:last-child span {
    font-size: 12px; }

.assignment-content span {
  display: block;
  font-size: 12px;
  line-height: 15px;
  color: #6C757D;
  font-weight: 400; }

.assignment-content p {
  font-size: 16px;
  line-height: 19px;
  color: #212529; }

.content-left {
  width: 34.8%; }
  .content-left li {
    border: 1px solid #CED4DA;
    border-top: none; }
  .content-left:first-child {
    border-top: 1px solid #CED4DA; }
  .content-left span {
    margin-bottom: 4px;
    text-align: left; }
  .content-left a {
    display: block;
    padding: 12px; }
    .content-left a:hover {
      background-color: #F8F9FA; }

.content-right {
  width: 63.52%;
  text-align: left; }
  .content-right li {
    padding: 16px 32px 22px 16px;
    border: 1px solid #CED4DA;
    border-top: none; }
    .content-right li:first-child {
      border-top: 1px solid #CED4DA;
      background-color: #F8F9FA;
      padding: 12px 16px; }

.content-tilte {
  margin-bottom: 16px; }
  .content-tilte img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 8px; }
  .content-tilte h2 {
    font-size: 16px;
    line-height: 19px;
    color: #212529;
    font-weight: bold; }
  .content-tilte span {
    margin-top: 4px; }

.content-left-word {
  padding-left: 48px; }
  .content-left-word p {
    font-size: 14px;
    line-height: 20px;
    font-weight: 400; }
  .content-left-word h2 {
    margin-left: 8px; }
  .content-left-word a {
    margin-top: 16px;
    width: 176px;
    padding: 8px 12px;
    border: 1px solid #CED4DA;
    background-color: #CED4DA;
    margin-bottom: 16.5px;
    color: #212529; }
    .content-left-word a:hover {
      opacity: 0.8; }
  .content-left-word .span-color {
    display: inline;
    color: #198754;
    font-size: 16px; }
    .content-left-word .span-color:hover {
      cursor: pointer; }

.border {
  border-bottom: 1px solid #CED4DA;
  max-width: 798px;
  margin: 0 auto; }

.content-left-footer {
  margin-top: 16.5px; }

.footer {
  margin-top: 16px; }

.assignment-page a {
  font-size: 16px;
  font-weight: bold;
  border: 1px solid #198754;
  color: #198754;
  background-color: white;
  padding: 8px 32px; }
  .assignment-page a:last-child {
    background-color: #198754;
    color: white;
    margin-left: 16px; }
    .assignment-page a:last-child:hover {
      cursor: pointer;
      background-color: #13613e;
      border: 1px solid #13613e; }
  .assignment-page a:first-child:focus {
    outline-color: #148214;
    outline-width: 3px;
    outline-style: solid;
    box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.8); }
  .assignment-page a:hover {
    background-color: #198754;
    cursor: pointer;
    color: white;
    transition: all 0.5s; }

.reply-title {
  background-color: #F8F9FA;
  border-top: 1px solid #CED4DA;
  border-bottom: 1px solid #CED4DA;
  margin: 12.5px -32px 0 -16px;
  padding: 18px 16px 19px; }
  .reply-title select {
    border: none;
    background-color: #F8F9FA;
    font-size: 16px;
    font-weight: bold;
    outline: 0; }
    .reply-title select:hover {
      cursor: pointer; }
  .reply-title a {
    margin-right: 12px; }
    .reply-title a:first-child {
      margin-left: 24px; }

textarea {
  border: none;
  outline: 0;
  margin-top: 16px;
  font-size: 16px;
  line-height: 1.5; }
  textarea::placeholder {
    color: #CED4DA; }
